:root {
  --main-color: black;
  --background-color: white;
  --accent-color: #101010;
  --page-margin-desktop: 20%;
  --page-margin-tablet: 15%;
  --page-margin-mobile: 10%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
}

a {
  color: black;
  -webkit-tap-highlight-color: transparent;
}

button {
  background: none;
  border: none;
}

button:focus {
  outline: 0;
}

/* Mobile Styles */

@media only screen and (max-width: 500px) {
  main {
    margin: 0 10% 100px 10%;
    margin: 0 var(--page-margin-mobile) 100px var(--page-margin-mobile);
  }
}

/* Tablet Styles */

@media only screen and (min-width: 500px) and (max-width: 1000px) {
  main {
    margin: 0 15% 100px 15%;
    margin: 0 var(--page-margin-tablet) 100px var(--page-margin-tablet);
  }
}

/* Desktop Styles */

@media only screen and (min-width: 1000px) {
  main {
    margin: 0 20% 100px 20%;
    margin: 0 var(--page-margin-desktop) 100px var(--page-margin-desktop);
  }
}
@-webkit-keyframes fadeIn {
    from {
        opacity: 0
    }
    to {
        opacity: 1
    }
}

@keyframes fadeIn {
    from {
        opacity: 0
    }
    to {
        opacity: 1
    }
}

@-webkit-keyframes slideInFromBelow {
    from {
        -webkit-transform: translateY(50%);
                transform: translateY(50%)
    }
    to {
        -webkit-transform: translateY(0%);
                transform: translateY(0%)
    }
}

@keyframes slideInFromBelow {
    from {
        -webkit-transform: translateY(50%);
                transform: translateY(50%)
    }
    to {
        -webkit-transform: translateY(0%);
                transform: translateY(0%)
    }
}

@-webkit-keyframes scaleHorizontal {
    from {
        -webkit-transform: scaleX(0%);
                transform: scaleX(0%)
    }
    to {
        -webkit-transform: scaleX(100%);
                transform: scaleX(100%)
    }
}

@keyframes scaleHorizontal {
    from {
        -webkit-transform: scaleX(0%);
                transform: scaleX(0%)
    }
    to {
        -webkit-transform: scaleX(100%);
                transform: scaleX(100%)
    }
}

.fadeIn {
    -webkit-animation: fadeIn 1s linear backwards;
            animation: fadeIn 1s linear backwards;
}

.slideInFromBelow {
    -webkit-animation: slideInFromBelow 1s ease backwards;
            animation: slideInFromBelow 1s ease backwards;
}

.fadeIn-slideInFromBelow {
    -webkit-animation: fadeIn .8s linear backwards, slideInFromBelow .8s ease backwards;
            animation: fadeIn .8s linear backwards, slideInFromBelow .8s ease backwards;
}

.scaleLeftToRight {
    -webkit-animation: scaleHorizontal .5s ease backwards;
            animation: scaleHorizontal .5s ease backwards;
    -webkit-transform-origin: 50% 0%;
            transform-origin: 50% 0%;
}
.navbar {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  height: 80px;
}

.navbar h1 {
  padding: 0;
  margin: 0;
  display: inline-block;
}

.navbar .logo {
  height: 50px;
}

.navbar-right a {
  position: relative;
  float: left;
  text-align: center;
  margin: 15px 0 15px 30px;
  text-decoration: none;
  font-size: 17px;
}

.navbar-right a:visited {
  color: var(--main-color);
}

.navbar-right #active:after {
  content: "";
  position: absolute;
  right: 0;
  bottom: -5px;
  width: 50%;
  height: 1px;
  background-color: var(--main-color);
  transition: cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.3s;
}

.navbar-right a:after {
  content: "";
  position: absolute;
  right: 0;
  bottom: -5px;
  background-color: var(--main-color);
  width: 0;
  height: 1px;
  transition: cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.3s;
}

.navbar-right a:hover:after {
  width: 50%;
  height: 1px;
}

.navbar-right {
  float: right;
}

/* Mobile Styles */

@media only screen and (max-width: 1000px) {
  .navbar {
    margin: 0 15px;
  }
  .navbar-right {
    display: none;
  }
}

