.motto-section {
    margin-bottom: 100px;
}

.motto-section-content {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin: 50px 0;
}

.motto-spacer {
    flex: 0.6;
}

.motto-connector {
    height: 50px;
    flex: 0.6;
}

.motto {
    flex: 1;
}

.motto-header {
    display: flex;
    justify-content: space-between;
}

.motto-header h4 {
    text-align: start;
    padding-left: 10px;
    border-left: 2px solid var(--main-color);
}

.motto-header img {
    height: 40px;
}

.motto-description {
    margin-top: 25px;
}

/* Mobile Styles */

@media only screen and (max-width: 500px) {
    .motto-section-content {
        flex-direction: column;
    }
    .motto {
        margin: 20px 0;
        padding: 0 10%;
    }
    .motto-connector {
        display: none;
    }
}

/* Tablet Styles */

@media only screen and (min-width: 500px) and (max-width: 1000px) {
    .motto-section-content {
        flex-direction: column;
    }
    .motto {
        margin: 25px 0;
        padding: 0 15%;
    }
    .motto-connector {
        display: none;
    }
}

/* Desktop Styles */

@media only screen and (min-width: 1000px) {
    .motto-section-content {
        padding: 0 5%;
    }
}